*{
  padding:0;
  margin:0;
  box-sizing: border-box;
}

h1{
  margin-bottom: 5vh;
}
.main{
  display: flex;
  justify-content: center;
}
.li-map{
  width: 20vw;
  list-style: none;
  display: flex;
  justify-content: space-between;
  margin-bottom: 2vh;
  border-left: 1px solid black;
  padding-left: 1vw;
}

.main-content{
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  margin-top:2vh;
}
.buttons{
  display: flex;
  justify-content: center;
  width: 20vw;
  margin-bottom: 2vh;
}
.add{
  border:none;
  background-color: green;
  width: 4vw;
  color:white;
  margin-left:1vw;

}
.btn{
  width: 2vw;
  border:none;
  background-color:red;
  color:white
}
.btn:hover{
  border:1px solid black;
}